.leadership-involvement-section {
  padding: 54px 0;
  text-align: center;
  background-color: #fff8f8; /* Matches other sections */
}

/* Carousel wrapper */
.work-carousel {
  position: relative;
}

.involvements-container {
  display: flex;
  flex-direction: row;
  gap: 24px;
  overflow-x: auto;
  scroll-snap-type: x mandatory;
  -webkit-overflow-scrolling: touch;
  padding: 24px calc(50% - (var(--card-width) / 2)); /* side padding allows edge centering */
}

.involvement-card {
  background-color: #ffffff; /* Lighter background for cards */
  border-radius: 8px;
  box-shadow: 0 3.6px 7.2px rgba(153, 30, 86, 0.16);
  padding: 27px;
  text-align: left;
  transition: all 0.3s ease;
  min-width: var(--card-min-width);
  flex: 0 0 var(--card-width);
  scroll-snap-align: center;
  transform: scale(0.96);
  opacity: 0.9;
  cursor: pointer;
}

.involvement-card h3 {
  font-size: 1.8rem;
  color: #000000; /* Lighter headings */
  margin-bottom: 9px;
}

.involvement-card:hover {
  transform: translateY(-4.5px) scale(1);
  box-shadow: 0 7.2px 14.4px rgba(153, 30, 86, 0.20);
}

.involvement-card.active {
  transform: scale(1);
  opacity: 1;
  box-shadow: 0 10px 24px rgba(153, 30, 86, 0.22);
}

.involvement-years,
.involvement-location {
  font-size: 1rem;
  color: #000000; /* Lighter grey for years/location */
  margin-bottom: 13.5px;
}

.involvement-description {
  list-style-type: disc;
  margin: 0; /* remove default left margin */
  padding-left: 0; /* remove default padding so bullets are not indented */
  list-style-position: inside; /* place bullets inside the content flow */
  font-size: 1.1rem;
  color: #000000; /* Light text */
}

.involvement-description li {
  margin-bottom: 7.2px;
}

/* Carousel controls (reuse the same styling pattern) */
.carousel-control {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: #ed3a3a;
  color: #fff;
  border: none;
  width: 40px;
  height: 40px;
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 10;
}

.carousel-control.prev {
  left: 6px;
}

.carousel-control.next {
  right: 6px;
}

/* Carousel indicators */
.carousel-indicators {
  display: flex;
  justify-content: center;
  gap: 12px;
  margin-top: 24px;
  padding: 0 20px;
}

.indicator-button {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: none;
  background-color: rgba(153, 30, 86, 0.3); /* semitransparent dark-pink */
  cursor: pointer;
  transition: background-color 300ms ease, transform 300ms ease;
  padding: 0;
}

.indicator-button:hover {
  background-color: rgba(153, 30, 86, 0.5);
  transform: scale(1.2);
}

.indicator-button.active {
  background-color: rgba(153, 30, 86, 0.7);
  transform: scale(1.3);
}