.navbar {
  background: #ffffff; /* solid white navbar */
  height: 72px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 999;
  box-shadow: none; /* removed shadow */
  transition: height 0.3s ease-in-out; /* keep height transition only */
}

.navbar-thin {
  height: 54px; /* Thin state (60 * 0.9) */
  background: #ffffff;
  box-shadow: none;
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 1080px; /* (1200 * 0.9) */
  padding: 0 21.6px; /* (0 24px * 0.9) */
}

.navbar-logo {
  color: #000000;
  text-decoration: none;
  font-size: 1.35rem; /* (1.5rem * 0.9) */
  font-weight: bold;
}

.menu-icon {
  display: none;
  font-size: 1.62rem; /* (1.8rem * 0.9) */
  cursor: pointer;
  color: #000000;
}

.nav-menu {
  display: flex;
  align-items: center;
  text-align: center;
}

.nav-item {
  color: #000000;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0 0.9rem; /* (0 1rem * 0.9) */
  height: 72px; /* Thick state for nav items (80 * 0.9) */
  cursor: pointer;
  transition: height 0.3s ease-in-out; /* Smooth transition */
}

.navbar-thin .nav-item {
  height: 54px; /* Thin state for nav items (60 * 0.9) */
}

.nav-item:hover {
  color: #61dafb;
  transition: all 0.2s ease-in-out;
}

@media screen and (max-width: 960px) {
  .menu-icon {
    display: block;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: calc(100vh - 72px); /* Thick mobile menu height (80 * 0.9) */
    position: absolute;
    top: 72px; /* Thick mobile menu top position (80 * 0.9) */
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease; /* Transition for slide-in */
    background: #ffffff; /* solid white mobile menu */
  }

  .navbar-menu-thin {
    height: calc(100vh - 54px); /* Thin mobile menu height (60 * 0.9) */
    top: 54px; /* Thin mobile menu top position (60 * 0.9) */
    background: #ffffff;
  }

  .nav-menu.active {
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-item {
    height: 54px; /* Default height for mobile nav items (60 * 0.9) */
    width: 100%;
    text-align: center;
    padding: 12px 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1rem;
    min-height: 44px;
    border-bottom: 1px solid #f0f0f0;
  }

  .nav-item:hover {
    background-color: #f1f1f1;
    color: #000000;
    transition: all 0.2s ease;
  }
}
